import firebase from "firebase/compat/app";
import "firebase/compat/database";
import firebaseConfig from "./firebaseConfig";

import "./Quiz.css";
import React, { useEffect, useState } from "react";
import ReactCardFlip from "react-card-flip";
import { Card } from "@mui/joy";

firebase.initializeApp(firebaseConfig);

function Quiz({ selectedQuestions }) {
  const [isFlipped, setIsFlipped] = useState([]);

  useEffect(() => {
    // 선택한 질문 개수에 따라 isFlipped 배열 초기화
    const initialFlippedState = Array(selectedQuestions.length).fill(false);
    setIsFlipped(initialFlippedState);
  }, [selectedQuestions]);

  const handleClick = (index) => {
    setIsFlipped((prevIsFlipped) => {
      const updatedIsFlipped = [...prevIsFlipped];
      updatedIsFlipped[index] = !updatedIsFlipped[index];
      return updatedIsFlipped;
    });
  };

  if (!selectedQuestions) {
    return <div>문제를 불러오는 중</div>;
  }

  const cardsData = selectedQuestions.map((question) => ({
    frontText: question.que,
    backText: question.ans,
  }));

  return (
    <div className="QAcardSet">
      {cardsData.slice(0, 3).map((card, index) => (
        <ReactCardFlip
          key={index}
          isFlipped={isFlipped[index]}
          flipDirection="vertical"
        >
          <Card className="Card_Front" onClick={() => handleClick(index)}>
            <div className="Card_Front_Que">{card.frontText}</div>
          </Card>
          <Card className="Card_Back" onClick={() => handleClick(index)}>
            <div className="Card_Back_Ans">{card.backText}</div>
          </Card>
        </ReactCardFlip>
      ))}
    </div>
  );
}

export default Quiz;
